<template lang="pug">
  .modal(ref="modalOrder")
    .modal__bg
    .modal__block
      slot.modal__text(name="text")
      .modal__btn.btn(@click="toggle")
        |OK
      .modal__close(@click="toggle")
        svg(viewBox="0 0 24 24")
          path(d="M17.414 6.586c-0.78-0.781-2.048-0.781-2.828 0l-2.586 2.586-2.586-2.586c-0.78-0.781-2.048-0.781-2.828 0-0.781 0.781-0.781 2.047 0 2.828l2.585 2.586-2.585 2.586c-0.781 0.781-0.781 2.047 0 2.828 0.39 0.391 0.902 0.586 1.414 0.586s1.024-0.195 1.414-0.586l2.586-2.586 2.586 2.586c0.39 0.391 0.902 0.586 1.414 0.586s1.024-0.195 1.414-0.586c0.781-0.781 0.781-2.047 0-2.828l-2.585-2.586 2.585-2.586c0.781-0.781 0.781-2.047 0-2.828z")
</template>
<style lang="scss">
  @import '../../assets/sass/vars';
  @import '../../assets/sass/mixins';
  .modal {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
  }
  .modal__bg {
    width: 100%;
    height: 100%;
    background-color: #ccc;
    opacity: .5;
    position: absolute;
    top: 0;
    left: 0;
  }
  .modal__block {
    padding: 20px 30px;
    background-color: $green-dark;
    color: #fff;
    position: relative;
    border-radius: 5px;
  }
  .modal__close {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 20px;
    height: 20px;
    &:hover {
      transform: scale(1.5)
    }
  }
  .modal__text {
    margin: 20px 0;
  }
  .close {
    display: none;
  }
</style>
<script>
export default {
  methods: {
    toggle () {
      this.$refs.modalOrder.classList.toggle('close')
    }
  }
}
</script>



