<template lang="pug">
  section.backet
    .container
      .backet__title Содержимое корзины
      .backet__table.table
        .table__thead
          .table__tr
            .table__td(v-for="(item, i) in thead", :key="i") {{ item }}
        .tablet__body
          .table__tr(v-for="(item, i) in basket", :key="i")
            .table__td
              img.table__img(:src="item.image")
              .table__item {{ item.name }}
              .table__delete-item
                svg(viewBox="0 0 24 24")
                  title delete
                  path(d="M12 3c-4.963 0-9 4.038-9 9s4.037 9 9 9 9-4.038 9-9-4.037-9-9-9zM12 19c-3.859 0-7-3.14-7-7s3.141-7 7-7 7 3.14 7 7-3.141 7-7 7z")
                  path(d="M12.707 12l2.646-2.646c0.194-0.194 0.194-0.512 0-0.707-0.195-0.194-0.513-0.194-0.707 0l-2.646 2.646-2.646-2.647c-0.195-0.194-0.513-0.194-0.707 0-0.195 0.195-0.195 0.513 0 0.707l2.646 2.647-2.646 2.646c-0.195 0.195-0.195 0.513 0 0.707 0.097 0.098 0.225 0.147 0.353 0.147s0.256-0.049 0.354-0.146l2.646-2.647 2.646 2.646c0.098 0.098 0.226 0.147 0.354 0.147s0.256-0.049 0.354-0.146c0.194-0.194 0.194-0.512 0-0.707l-2.647-2.647z")
            .table__td
              .table__count-wrap
                .table__count(v-model="count")
                .table__count-change
                  .table__change +
                  .table__change -
            .table__td {{ item.price }} RUB
            .table__td {{ item.price }} RUB
        .table__total Итоговая стоимость {{ }} RUB
        .table__btn
          .btn Оформить заказ
</template>
<style lang="scss">
  @import '../../assets/sass/vars';
  @import '../../assets/sass/mixins';
  .backet {
    flex-grow: 2;
    margin-top: 80px;
  }
  .backet__title {
    font-size: 18px;
    font-weight: 700;
    color: $green-dark;
    margin-bottom: 30px;
  }
  .table {
    display: flex;
    flex-direction: column;
  }
  .table__thead {
    text-align: center;
  }
  .table__td {
    border: 1px solid;
    flex-basis: 15%;
    padding: 10px 5px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    &:first-child {
      flex-basis: 55%;
      justify-content: flex-start;
    }
  }
  .table__tr {
    display: flex;
  }
  .table__img {
    width: 100px;
    height: 100px;
    margin-right: 20px;
  }
  .table__item {
    font-size: 18px;
    margin-right: 10px;
    color: $green-dark;
  }
  .table__delete-item {
    width: 24px;
    height: 24px;
    transition: .3s;
    svg {
      fill: $hover;
    }
    &:hover svg {
      fill: $greey-dark;
      transform: scale(1.2);
    }
  }
  .table__thead .table__td:first-child {
    padding-left: 105px;
  }
  .table__count-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
  }
  .table__count {
    width: 40px;
    height: 40px;
    border: 1px solid $green-light;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .table__count-change {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
    font-size: 18px;
    font-weight: 700;
    width: 40px;
    height: 40px;
  }
  .table__change:hover {
    transform: scale(1.2);
  }
  .table__total {
    text-align: right;
    color: $greey-dark;
    font-size: 18px;
    font-weight: 700;
    border-top: 1px solid $down;
    border-bottom: 1px solid $down;
    margin: 25px 0;
    padding: 20px 0;
  }
  .table__btn {
    display: flex;
    justify-content: flex-end;
    .btn {
      width: auto;
      display: inline-block;

    }
  }
</style>
<script>
export default {
  data () {
    return {
      thead: ['Товар', 'Кол-во', 'Цена за ед.', 'Итого'],
      count: 1
    }
  },
  computed: {
    basket () {
      return this.$store.state.basket
    }
    // totalForItem () {
      // return
    // }
  }
}
</script>



