<template lang="pug">
  .modal(v-if="isShow", ref="modalReview")
    .modal__bg
    .modal__block
      .modal__text Оставьте ваш отзыв:
      .modal__close(@click="close")
        svg(viewBox="0 0 24 24")
          path(d="M17.414 6.586c-0.78-0.781-2.048-0.781-2.828 0l-2.586 2.586-2.586-2.586c-0.78-0.781-2.048-0.781-2.828 0-0.781 0.781-0.781 2.047 0 2.828l2.585 2.586-2.585 2.586c-0.781 0.781-0.781 2.047 0 2.828 0.39 0.391 0.902 0.586 1.414 0.586s1.024-0.195 1.414-0.586l2.586-2.586 2.586 2.586c0.39 0.391 0.902 0.586 1.414 0.586s1.024-0.195 1.414-0.586c0.781-0.781 0.781-2.047 0-2.828l-2.585-2.586 2.585-2.586c0.781-0.781 0.781-2.047 0-2.828z")
      form.modal__form(@submit.prevent="saveReview")
        Input(:type="user.type", :name="user.name", :text="user.text", v-model="formUser", @input="inputUser($event)")
        Input(:type="email.type", :name="email.name", :text="email.text", v-model="formEmail", @input="inputEmail($event)")
        Input(:type="review.type", :name="review.name", :text="review.text", v-model="formText", @input="inputText($event)")
        .modal__btn
          input.btn(type="submit" value="Оставить отзыв")
        p.modal__text(v-if="success") Ваш отзыв отправлен!

</template>
<style lang="scss">
  @import '../../assets/sass/vars';
  @import '../../assets/sass/mixins';
  .modal {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 30;
  }
  .modal__bg {
    width: 100%;
    height: 100%;
    background-color: #ccc;
    opacity: .5;
    position: absolute;
    top: 0;
    left: 0;
  }
  .modal__block {
    padding: 20px 30px;
    background-color: $green-dark;
    color: #fff;
    position: relative;
    border-radius: 5px;
  }
  .modal__close {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 20px;
    height: 20px;
    &:hover {
      transform: scale(1.5)
    }
  }
  .modal__text {
    margin: 20px 0;
  }
  .modal__btn {
    display: flex;
    justify-content: center;
  }
  .close {
    display: none;
  }
</style>
<script>
import Input from '../Order/Input.vue'
export default {
  data () {
    return {
      user: {type: 'type', name: 'user', text: 'ФИО'},
      email: {type: 'email', name: 'email', text: 'Email'},
      review: {type: 'textarea', name: 'review', text: 'Ваш отзыв'},
      formUser: undefined,
      formEmail: undefined,
      formText: undefined,
      success: false,
      value: ''
    }
  },
  components: {
    Input
  },
  props: {
    isShow: Boolean
  },
  methods: {
    close () {
      this.$emit('close')
    },
    saveReview () {
      const data = {
        user: this.formUser,
        email: this.formEmail,
        text: this.formText
      }
      console.log(111, data)
      this.$store.dispatch('saveReview', data)
        .then(() => {
          this.success = true
        })
    },
    inputUser (e) {
      // console.log(333, e)
      this.formUser = e
    },
    inputEmail (e) {
      // console.log(333, e)
      this.formEmail = e
    },
    inputText (e) {
      // console.log(333, e)
      this.formText = e
    }
  }
}
</script>



