export default {
  data () {
    return {
      // count: 'Пока пуста',
      navTop: [
        { href: '/catalog', name: 'Каталог товаров' },
        { href: '/order', name: 'Вязание на заказ' }
      ],
      navBottom: [
        { href: '/', name: 'Новое поступление' },
        { href: '/', name: 'Распродажа' }
      ],
      hamburger: this.$refs.hamburger
    }
  },
  computed: {
    count () {
      return this.$store.state.basket ? this.$store.state.basket.length : 'Пока пуста'
    }
  },
  methods: {
    open () {
      const hamburger = this.$refs.hamburger
      const menu = this.$refs.menu

      hamburger.classList.toggle('is-active')
      menu.classList.toggle('is-active')
    }
  }
}



